.mainTitle{width:100%;position:relative;font-family:"Poppins","NotoSansKR",-apple-system,BlinkMacSystemFont,"Malgun Gothic","맑은 고딕",helvetica,"Apple SD Gothic Neo",helvetica,"나눔바른고딕 옛한글","NanumBarunGothic YetHangul",sans-serif}.mainTitle .mainMind{height:100vh;font-size:100px;overflow:hidden}.mainTitle .mainMind{position:absolute;top:45vh;left:10vw;font-weight:700}.mainTitle .mainIcon{position:relative;top:74.5vh;left:32vw;width:25px;animation:rot 1s .3s infinite alternate;transform-origin:30% -60%}.mainTitle .mainIcon .crossTop::after{content:"";border-top:40px solid red;border-right:15px solid red;transform:rotate(45deg)}.mainTitle .mainIcon .crossBottom::after{content:"";position:absolute;top:-19px;left:-25px;border-top:15px solid red;border-right:67px solid red}@keyframes rot{0%{transform:translate(-30%, -30%) rotate(0deg)}100%{transform:translate(-30%, -30%) rotate(360deg)}}