.navbar{width:100%;height:100px;font-family:"Poppins","NotoSansKR",-apple-system,BlinkMacSystemFont,"Malgun Gothic","맑은 고딕",helvetica,"Apple SD Gothic Neo",helvetica,"나눔바른고딕 옛한글","NanumBarunGothic YetHangul",sans-serif;box-shadow:rgba(0,0,0,.16) 0px 3px 6px,rgba(0,0,0,.23) 0px 3px 6px;position:fixed;z-index:9999;background-color:#fff}.navbarIcon{display:none}.logo{height:100px;font-size:22px;text-align:center;font-weight:bold;position:absolute;left:70px;top:26px}.logo img{width:80px}.logo a{display:block;text-decoration:none;color:red}.navList{display:flex;text-transform:uppercase;position:absolute;top:30px;right:50px}.navList .content{--padding: 1.5rem;font-size:2em;position:relative;cursor:pointer}.navList .content a{color:#131313;text-decoration:none;padding:70px;font-size:20px;font-weight:bold;transition:.3s}.navList .content a:hover{color:#666}.navList .content::after{content:"";position:absolute;left:0;bottom:-1.5rem;height:3px;width:100%;background:red}@media(hover){.navList .content:hover::after{transform:scaleX(1);margin-left:0}.navList .content::after{transform:scaleX(0);margin-left:50%;transform-origin:left;transition:transform 500ms ease,margin-left .5s ease}}@media screen and (max-width: 720px){.navbarIcon{display:block;position:absolute;right:20px;top:35px;background-color:#fff;border:none}.navbarIcon img{display:block;color:red;width:30px;height:30px}.logo{position:absolute;left:20px}.navList li{display:none}}