@import "variable.scss";

.marquee {
  height: 25px;
  width: 100%;
  margin: 0;
  text-transform: uppercase;

  overflow: hidden;
  position: relative;
  top: 505vh;
  left: 0vw;
  background-color: #666;
  color: #fff;
  font-family: "Poppins";
  font-weight: bold;
  padding: 10px 0 0 0;
}

.marquee div {
  display: block;
  width: 200%;
  height: 60px;

  position: absolute;
  overflow: hidden;

  animation: marquee 15s linear infinite;
}

.marquee span {
  float: left;
  width: 50%;
}
span {
  letter-spacing: 2.5px;
}
@keyframes marquee {
  0% {
    left: 0;
  }
  100% {
    left: -100%;
  }
}
